import { useState } from "react";
import { useTranslation } from "react-i18next";
import { formatDistanceToNow } from "date-fns";
import { zhCN, enUS, pt } from "date-fns/locale";
import { Clock, Users, Utensils, Flame, ChefHat, Heart, MessageCircle, Share2, List, PlayCircle } from "lucide-react";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";
import { CommunityPost } from "@/hooks/useCommunityPosts";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import { RecipeStep, Ingredient, NutritionInfo } from "./RecipeTypes";
import RecipeOverview from "./RecipeOverview";
import RecipeCookingMode from "./RecipeCookingMode";
import TranslateButton from "@/components/TranslateButton";

interface ExtendedPost extends CommunityPost {
  post_type?: string;
  recipe_name?: string;
  recipe_name_en?: string;
  recipe_name_pt?: string;
  cook_time?: number;
  difficulty?: string;
  servings?: number;
  ingredients?: Ingredient[];
  steps?: RecipeStep[];
  nutrition?: NutritionInfo;
  recipe_tags?: string[];
  recipe_category?: string;
}

interface RecipeDetailContentProps {
  post: ExtendedPost;
  showActions?: boolean;
}

type ViewMode = 'guided' | 'full';
type GuidedPhase = 'overview' | 'cooking';

const RecipeDetailContent = ({ post, showActions = false }: RecipeDetailContentProps) => {
  const { t, i18n } = useTranslation();
  const { getLocalizedField } = useLocalizedContent();
  
  // View mode: 'guided' (step-by-step) or 'full' (all at once)
  const [viewMode, setViewMode] = useState<ViewMode>('guided');
  // For guided mode: 'overview' (info + ingredients) or 'cooking' (steps)
  const [guidedPhase, setGuidedPhase] = useState<GuidedPhase>('overview');
  // Translation state
  const [translatedName, setTranslatedName] = useState<string | null>(null);
  const [translatedContent, setTranslatedContent] = useState<string | null>(null);
  const [translatedIngredients, setTranslatedIngredients] = useState<Record<number, string>>({});
  const [translatedSteps, setTranslatedSteps] = useState<Record<number, string>>({});

  // Get localized content - prioritize pre-stored translations
  const getLocalizedContent = () => {
    const langMap: Record<string, string> = {
      'zh': 'content_zh',
      'en': 'content_en', 
      'pt': 'content_pt'
    };
    const field = langMap[i18n.language] || 'content';
    const localizedValue = post[field as keyof typeof post];
    if (localizedValue && typeof localizedValue === 'string' && localizedValue.trim()) {
      return localizedValue;
    }
    return post.content;
  };

  const displayContent = translatedContent || getLocalizedContent();
  const displayName = translatedName || getLocalizedField(post, "recipe_name") || post.recipe_name;

  // Build combined text for translation: name + content + ingredients + steps
  const buildTranslationText = () => {
    const parts: string[] = [];
    if (post.recipe_name) {
      parts.push(`[NAME]${post.recipe_name}`);
    }
    if (post.content && post.content.trim() && !post.content.includes('主要配料：')) {
      parts.push(`[CONTENT]${post.content}`);
    }
    if (post.ingredients && post.ingredients.length > 0) {
      const ingredientNames = post.ingredients.map(i => i.name).join('|');
      parts.push(`[INGREDIENTS]${ingredientNames}`);
    }
    if (post.steps && post.steps.length > 0) {
      const stepDescs = post.steps.sort((a, b) => a.order - b.order).map(s => s.description).join('|');
      parts.push(`[STEPS]${stepDescs}`);
    }
    return parts.join('\n---\n');
  };

  // Parse translation result
  const handleTranslation = (translated: string | null) => {
    if (!translated) {
      setTranslatedName(null);
      setTranslatedContent(null);
      setTranslatedIngredients({});
      setTranslatedSteps({});
      return;
    }
    
    const sections = translated.split('\n---\n');
    sections.forEach(section => {
      if (section.startsWith('[NAME]')) {
        setTranslatedName(section.replace('[NAME]', ''));
      } else if (section.startsWith('[CONTENT]')) {
        setTranslatedContent(section.replace('[CONTENT]', ''));
      } else if (section.startsWith('[INGREDIENTS]')) {
        const names = section.replace('[INGREDIENTS]', '').split('|');
        const map: Record<number, string> = {};
        names.forEach((name, idx) => { map[idx] = name.trim(); });
        setTranslatedIngredients(map);
      } else if (section.startsWith('[STEPS]')) {
        const descs = section.replace('[STEPS]', '').split('|');
        const map: Record<number, string> = {};
        descs.forEach((desc, idx) => { map[idx] = desc.trim(); });
        setTranslatedSteps(map);
      }
    });
  };

  const getDateLocale = () => {
    switch (i18n.language) {
      case "zh": return zhCN;
      case "pt": return pt;
      default: return enUS;
    }
  };

  const getDifficultyColor = (d: string) => {
    switch (d) {
      case 'easy': return 'bg-green-500/20 text-green-700 dark:text-green-400 border-green-500/30';
      case 'medium': return 'bg-yellow-500/20 text-yellow-700 dark:text-yellow-400 border-yellow-500/30';
      case 'hard': return 'bg-red-500/20 text-red-700 dark:text-red-400 border-red-500/30';
      default: return 'bg-muted text-muted-foreground';
    }
  };

  const getDifficultyLabel = (d: string) => {
    switch (d) {
      case 'easy': return t("recipe.difficulty.easy", "简单");
      case 'medium': return t("recipe.difficulty.medium", "中等");
      case 'hard': return t("recipe.difficulty.hard", "困难");
      default: return d;
    }
  };

  const hasImages = post.image_urls && post.image_urls.length > 0;
  const hasSteps = post.steps && post.steps.length > 0;

  // Guided mode - Cooking phase (full screen step-by-step)
  if (viewMode === 'guided' && guidedPhase === 'cooking' && hasSteps) {
    return (
      <Card className="overflow-hidden">
        <RecipeCookingMode 
          steps={post.steps!} 
          onExit={() => setGuidedPhase('overview')} 
        />
      </Card>
    );
  }

  // Guided mode - Overview phase
  if (viewMode === 'guided') {
    return (
      <div className="space-y-4">
        {/* Mode toggle */}
        <div className="flex justify-end">
          <div className="inline-flex h-9 items-center rounded-md bg-muted p-1 text-muted-foreground">
            <button
              onClick={() => setViewMode('guided')}
              className="inline-flex items-center justify-center whitespace-nowrap rounded-sm px-3 py-1.5 text-sm font-medium transition-all gap-1.5 bg-background text-foreground shadow-sm"
            >
              <PlayCircle className="h-4 w-4" />
              {t("recipe.guidedMode", "流程模式")}
            </button>
            <button
              onClick={() => setViewMode('full')}
              className="inline-flex items-center justify-center whitespace-nowrap rounded-sm px-3 py-1.5 text-sm font-medium transition-all gap-1.5"
            >
              <List className="h-4 w-4" />
              {t("recipe.fullMode", "完整模式")}
            </button>
          </div>
        </div>

        <RecipeOverview 
          post={post} 
          onStartCooking={() => setGuidedPhase('cooking')} 
        />

        {/* Stats */}
        <Card>
          <CardContent className="py-4">
            <div className="flex items-center gap-6 text-muted-foreground">
              <span className="flex items-center gap-1.5 text-sm">
                <Heart className="h-4 w-4" />
                {post.likes_count || 0}
              </span>
              <span className="flex items-center gap-1.5 text-sm">
                <MessageCircle className="h-4 w-4" />
                {post.comments_count || 0}
              </span>
              <span className="flex items-center gap-1.5 text-sm">
                <Share2 className="h-4 w-4" />
                {post.shares_count || 0}
              </span>
            </div>
          </CardContent>
        </Card>
      </div>
    );
  }

  // Full mode - Original layout with all content
  return (
    <div className="space-y-4">
      {/* Mode toggle */}
      <div className="flex justify-end">
        <div className="inline-flex h-9 items-center rounded-md bg-muted p-1 text-muted-foreground">
          <button
            onClick={() => {
              setViewMode('guided');
              setGuidedPhase('overview');
            }}
            className="inline-flex items-center justify-center whitespace-nowrap rounded-sm px-3 py-1.5 text-sm font-medium transition-all gap-1.5"
          >
            <PlayCircle className="h-4 w-4" />
            {t("recipe.guidedMode", "流程模式")}
          </button>
          <button
            onClick={() => setViewMode('full')}
            className="inline-flex items-center justify-center whitespace-nowrap rounded-sm px-3 py-1.5 text-sm font-medium transition-all gap-1.5 bg-background text-foreground shadow-sm"
          >
            <List className="h-4 w-4" />
            {t("recipe.fullMode", "完整模式")}
          </button>
        </div>
      </div>

      <Card className="overflow-hidden">
        {/* Hero Image */}
        {hasImages && (
          <div className="relative aspect-video overflow-hidden">
            <img
              src={post.image_urls![0]}
              alt={displayName || ""}
              className="w-full h-full object-cover"
            />
            <Badge className="absolute top-3 left-3 bg-primary text-primary-foreground gap-1">
              <Utensils className="h-3 w-3" />
              {t("recipe.badge", "食谱")}
            </Badge>
          </div>
        )}

        <CardHeader className="pb-3">
          {/* Author */}
          <div className="flex items-center gap-3 mb-4">
            <Avatar className="h-10 w-10">
              <AvatarImage src={post.author?.avatar_url || undefined} />
              <AvatarFallback>
                {post.author?.display_name?.charAt(0) || "?"}
              </AvatarFallback>
            </Avatar>
            <div>
              <p className="font-medium text-sm">{post.author?.display_name}</p>
              <p className="text-xs text-muted-foreground">
                {formatDistanceToNow(new Date(post.created_at), {
                  addSuffix: true,
                  locale: getDateLocale(),
                })}
              </p>
            </div>
          </div>

          {/* Translate All Button - above recipe name */}
          <div className="mb-2">
            <TranslateButton 
              text={buildTranslationText()} 
              onTranslation={handleTranslation}
              label={t("recipe.translateAllFull")}
            />
          </div>

          {/* Recipe Name */}
          <h2 className="text-xl font-bold">{displayName}</h2>

          {/* Meta Info */}
          <div className="flex flex-wrap items-center gap-3 mt-3">
            {post.cook_time && (
              <div className="flex items-center gap-1.5 text-sm text-muted-foreground">
                <Clock className="h-4 w-4" />
                <span>{post.cook_time} {t("recipe.minutes", "分钟")}</span>
              </div>
            )}
            {post.servings && (
              <div className="flex items-center gap-1.5 text-sm text-muted-foreground">
                <Users className="h-4 w-4" />
                <span>{post.servings} {t("recipe.servings", "人份")}</span>
              </div>
            )}
            {post.difficulty && (
              <Badge variant="outline" className={getDifficultyColor(post.difficulty)}>
                <ChefHat className="h-3 w-3 mr-1" />
                {getDifficultyLabel(post.difficulty)}
              </Badge>
            )}
          </div>
        </CardHeader>

        <CardContent className="space-y-6">
          {/* Description - only show if there's actual content */}
          {post.content && post.content.trim() && !post.content.includes('主要配料：') && (
            <div>
              <p className="text-sm text-muted-foreground whitespace-pre-line">
                {displayContent}
              </p>
            </div>
          )}

          {/* Nutrition Info */}
          {post.nutrition && (post.nutrition.calories || post.nutrition.protein || post.nutrition.carbs || post.nutrition.fat) && (
            <div>
              <h3 className="font-semibold text-sm mb-3 flex items-center gap-2">
                <Flame className="h-4 w-4 text-primary" />
                {t("recipe.nutrition", "营养信息")}
              </h3>
              <div className="grid grid-cols-4 gap-2">
                {post.nutrition.calories && (
                  <div className="text-center p-2 bg-muted/50 rounded-lg">
                    <p className="text-lg font-bold text-primary">{post.nutrition.calories}</p>
                    <p className="text-xs text-muted-foreground">{t("recipe.kcal", "千卡")}</p>
                  </div>
                )}
                {post.nutrition.protein && (
                  <div className="text-center p-2 bg-muted/50 rounded-lg">
                    <p className="text-lg font-bold text-primary">{post.nutrition.protein}g</p>
                    <p className="text-xs text-muted-foreground">{t("recipe.protein", "蛋白质")}</p>
                  </div>
                )}
                {post.nutrition.carbs && (
                  <div className="text-center p-2 bg-muted/50 rounded-lg">
                    <p className="text-lg font-bold text-primary">{post.nutrition.carbs}g</p>
                    <p className="text-xs text-muted-foreground">{t("recipe.carbs", "碳水")}</p>
                  </div>
                )}
                {post.nutrition.fat && (
                  <div className="text-center p-2 bg-muted/50 rounded-lg">
                    <p className="text-lg font-bold text-primary">{post.nutrition.fat}g</p>
                    <p className="text-xs text-muted-foreground">{t("recipe.fat", "脂肪")}</p>
                  </div>
                )}
              </div>
            </div>
          )}

          <Separator />

          {/* Ingredients */}
          {post.ingredients && post.ingredients.length > 0 && (
            <div>
              <h3 className="font-semibold text-sm mb-3">
                {t("recipe.ingredients", "食材")} ({post.ingredients.length})
              </h3>
              <ul className="space-y-2">
                {post.ingredients.map((ingredient, index) => (
                  <li key={index} className="flex items-center justify-between text-sm py-1.5 border-b border-border/50 last:border-0">
                    <span>{translatedIngredients[index] || ingredient.name}</span>
                    <span className="text-muted-foreground ml-2 shrink-0">
                      {ingredient.amount} {ingredient.unit}
                    </span>
                  </li>
                ))}
              </ul>
            </div>
          )}

          <Separator />

          {/* Steps */}
          {hasSteps && (
            <div>
              <h3 className="font-semibold text-sm mb-3">
                {t("recipe.steps", "步骤")} ({post.steps!.length})
              </h3>
              <ol className="space-y-4">
                {post.steps!
                  .sort((a, b) => a.order - b.order)
                  .map((step, index) => (
                    <li key={index} className="flex gap-3">
                      <div className="flex-shrink-0 w-6 h-6 rounded-full bg-primary text-primary-foreground flex items-center justify-center text-xs font-bold">
                        {step.order}
                      </div>
                      <div className="flex-1 space-y-2">
                        <p className="text-sm">{translatedSteps[index] || step.description}</p>
                        {step.image_url && (
                          <img
                            src={step.image_url}
                            alt={`Step ${step.order}`}
                            className="w-full rounded-lg max-h-48 object-cover"
                          />
                        )}
                      </div>
                    </li>
                  ))}
              </ol>
            </div>
          )}

          {/* Tags */}
          {post.recipe_tags && post.recipe_tags.length > 0 && (
            <div className="flex flex-wrap gap-2 pt-2">
              {post.recipe_tags.map((tag, index) => (
                <Badge key={index} variant="secondary" className="text-xs">
                  #{tag}
                </Badge>
              ))}
            </div>
          )}

          {/* Stats */}
          <div className="flex items-center gap-6 pt-4 border-t text-muted-foreground">
            <span className="flex items-center gap-1.5 text-sm">
              <Heart className="h-4 w-4" />
              {post.likes_count || 0}
            </span>
            <span className="flex items-center gap-1.5 text-sm">
              <MessageCircle className="h-4 w-4" />
              {post.comments_count || 0}
            </span>
            <span className="flex items-center gap-1.5 text-sm">
              <Share2 className="h-4 w-4" />
              {post.shares_count || 0}
            </span>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default RecipeDetailContent;
