import { useState } from "react";
import { useTranslation } from "react-i18next";
import { ChevronLeft, ChevronRight, Check, X } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Progress } from "@/components/ui/progress";
import { RecipeStep } from "./RecipeTypes";
import TranslateButton from "@/components/TranslateButton";

interface RecipeCookingModeProps {
  steps: RecipeStep[];
  onExit: () => void;
}

const RecipeCookingMode = ({ steps, onExit }: RecipeCookingModeProps) => {
  const { t } = useTranslation();
  const [currentStep, setCurrentStep] = useState(0);
  const [translatedSteps, setTranslatedSteps] = useState<Record<number, string>>({});
  
  const sortedSteps = [...steps].sort((a, b) => a.order - b.order);
  const totalSteps = sortedSteps.length;
  const step = sortedSteps[currentStep];
  const progress = ((currentStep + 1) / totalSteps) * 100;
  const isLastStep = currentStep === totalSteps - 1;
  const isFirstStep = currentStep === 0;

  const handleNext = () => {
    if (!isLastStep) {
      setCurrentStep(prev => prev + 1);
    }
  };

  const handlePrev = () => {
    if (!isFirstStep) {
      setCurrentStep(prev => prev - 1);
    }
  };

  const handleComplete = () => {
    onExit();
  };

  return (
    <div className="flex flex-col h-full min-h-[60vh]">
      {/* Header */}
      <div className="flex items-center justify-between px-4 py-3 border-b bg-background sticky top-0 z-10">
        <Button variant="ghost" size="icon" onClick={onExit}>
          <X className="h-5 w-5" />
        </Button>
        <span className="font-medium">
          {t("recipe.stepProgress", "步骤 {{current}} / {{total}}", { 
            current: currentStep + 1, 
            total: totalSteps 
          })}
        </span>
        <TranslateButton 
          text={sortedSteps.map(s => s.description).join('\n---\n')} 
          onTranslation={(translated) => {
            if (translated) {
              const descriptions = translated.split('\n---\n');
              const map: Record<number, string> = {};
              descriptions.forEach((desc, idx) => { map[idx] = desc; });
              setTranslatedSteps(map);
            } else {
              setTranslatedSteps({});
            }
          }}
        />
      </div>

      {/* Progress bar */}
      <Progress value={progress} className="h-1 rounded-none" />

      {/* Step content */}
      <div className="flex-1 overflow-y-auto p-6">
        {/* Step number badge */}
        <div className="flex items-center justify-center mb-6">
          <div className="w-16 h-16 rounded-full bg-primary text-primary-foreground flex items-center justify-center text-2xl font-bold shadow-lg">
            {step.order}
          </div>
        </div>

        {/* Step image */}
        {step.image_url && (
          <div className="mb-6 rounded-xl overflow-hidden">
            <img
              src={step.image_url}
              alt={`Step ${step.order}`}
              className="w-full h-auto max-h-64 object-cover"
            />
          </div>
        )}

        {/* Step description */}
        <p className="text-lg leading-relaxed text-center">
          {translatedSteps[currentStep] || step.description}
        </p>
      </div>

      {/* Step dots indicator */}
      <div className="flex justify-center gap-1.5 py-3">
        {sortedSteps.map((_, index) => (
          <button
            key={index}
            className={`w-2 h-2 rounded-full transition-all ${
              index === currentStep 
                ? 'bg-primary w-6' 
                : index < currentStep 
                  ? 'bg-primary/50' 
                  : 'bg-muted-foreground/30'
            }`}
            onClick={() => setCurrentStep(index)}
          />
        ))}
      </div>

      {/* Navigation buttons */}
      <div className="flex items-center justify-between p-4 border-t bg-background">
        <Button
          variant="outline"
          size="lg"
          onClick={handlePrev}
          disabled={isFirstStep}
          className="gap-2"
        >
          <ChevronLeft className="h-5 w-5" />
          {t("recipe.prevStep", "上一步")}
        </Button>

        {isLastStep ? (
          <Button
            size="lg"
            onClick={handleComplete}
            className="gap-2"
            variant="default"
          >
            <Check className="h-5 w-5" />
            {t("recipe.complete", "完成")}
          </Button>
        ) : (
          <Button
            size="lg"
            onClick={handleNext}
            className="gap-2"
          >
            {t("recipe.nextStep", "下一步")}
            <ChevronRight className="h-5 w-5" />
          </Button>
        )}
      </div>
    </div>
  );
};

export default RecipeCookingMode;
